import React, { Component } from 'react';
import { graphql, StaticQuery } from 'gatsby'
import styled from "styled-components"
import heroStyles from "../components/hero.module.scss"

import BackgroundImage from 'gatsby-background-image'

const BackgroundHero = ({ className }) => (
    <StaticQuery
        query={graphql`
            query {
                desktop: file(relativePath: { eq: "hero-bg.jpg"}) {
                    childImageSharp {
                        fluid(quality: 90, maxWidth: 1920) {
                            ...GatsbyImageSharpFluid_withWebp
                        }
                    }
                }
            }
        `}
        render={data => {
            // Set ImageData
            const imageData = data.desktop.childImageSharp.fluid
            return (
                <div>
                    <BackgroundImage
                        Tag="section"
                        className={className, heroStyles.hero}
                        fluid={imageData}
                        
                    >

                    </BackgroundImage>
                    <div className={heroStyles.bgOverlay}></div>
                    <div className={heroStyles.poly}></div>
                </div>
            )
        }}
    />
)

const StyledBackgroundHero = styled(BackgroundHero)`

`

export default StyledBackgroundHero;
